@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: "Raleway", sans-serif !important;
  font-size: 16px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.text-orange{
  color: #F8333C;
}

.header-text{
  font-size: 26px;
  font-weight: bold;
}

.title-text{
  font-size: 18px;
  font-weight: bold;
}

.btn-primary-outline_white{
  font-weight: 900;
  color: #fff;
  border: 1px solid #fff;
  padding: 10px 15px;
  border-radius: 20px;
}

.btn-primary-outline_white:hover {
  background-color: #fff;
  color: #F8333C;
  text-decoration: none;
  -webkit-transition: 300ms ease;
  transition: 300ms ease;
}
.app{
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
}

.app-header{
  height: 120px;
  padding: 20px 15%;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color:#fff;
  z-index: 5;
  -webkit-animation: slide-down 800ms forwards ease-in-out;
          animation: slide-down 800ms forwards ease-in-out;
}

.expand-down{
  -webkit-animation: expand-down 700ms forwards ease-out;
          animation: expand-down 700ms forwards ease-out;
}

.humburger{
  position: absolute;
  display: none;
  cursor: pointer;
  top: 20px;
  right: 0;
}

.nav-item{
  padding: 5px 10px;
}
.nav-item a{
  font-size: 18px;
}

.nav-item a:hover{
  text-decoration: none;
}
.nav-item:hover a{
  padding: 10px 15px;
  background-color: #F8333C;
  border-radius: 10px;
  color: white;
  -webkit-transition:500ms ease-out;
  transition:500ms ease-out;
}

.cs-clink{
  opacity: 1;
  font-weight: 400;
  color: #424242;
  -webkit-animation: cursor-blink 1200ms infinite forwards;
          animation: cursor-blink 1200ms infinite forwards;
}

.face-wrap{
  width: 40%;
}
.quote{
  width: 60%;
}

.quote-wrap{
  width: 100%;
  -webkit-animation: slide-left 800ms forwards ease-in-out;
          animation: slide-left 800ms forwards ease-in-out;
}

.greet{
  margin-top: 160px;
  height: 150px;
}
.happy-face-slide-animate{
  width: 120px;
  height: 120px;
  -webkit-animation: slide-right 800ms forwards ease-in-out;
          animation: slide-right 800ms forwards ease-in-out;
}


.happy-face{
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-animation: logo-rotate 4000ms 1200ms infinite  forwards ease-in-out;
          animation: logo-rotate 4000ms 1200ms infinite  forwards ease-in-out;
}

.smile{
  position: absolute;
  top:65%;
  left: 50%;
  width: 65%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-animation: smile 4000ms 2500ms infinite forwards ease-in;
          animation: smile 4000ms 2500ms infinite forwards ease-in;
}

.greet-quote{
  font-size: 36px;
  font-weight: bold;
}

.tech-quote{
  font-size: 32px;
  font-weight: bold;
}

.moved-down{
  -webkit-animation: move-down 700ms forwards ease-out;
          animation: move-down 700ms forwards ease-out;
}

.hero{
  width: 60%;
  -webkit-animation: slide-up 800ms forwards ease-in-out;
          animation: slide-up 800ms forwards ease-in-out;
}

@media screen and (max-width:767px){
  .app{
    height: auto;
    margin-bottom: 50px;
  }
  .logo img{
    height: 50% !important;
  }

  .greet{
    height: 100px;
    line-height: 1;
  }
  .face-wrap{
    width: 35%;
  }
  .quote{
    width: 65%;
  }
  .happy-face-slide-animate{
    width: 80px;
    height: 80px;
  }

  .happy-face{
   margin-right: 20px
  }
   .greet-quote{
     font-size: 26px;
   }

   .tech-quote{
     font-size: 22px;
   }

   .humburger{
     display: block;
   }

   .collapsed-navbar{
    position: absolute;
    top: 0px;
    left:50%;
    -webkit-transform: translateX(-70%);
            transform: translateX(-70%);
    -webkit-animation: drop-down 600ms forwards ease-in;
            animation: drop-down 600ms forwards ease-in;
  }
  
  .collapsed-navbar .nav-item a{
    font-size: 18px;
    padding: 10px 15px;
  }
  
  .hero{
    width: 80%;
  }
}
@media screen and (max-width:576px){
  .happy-face-slide-animate{
    width: 60px;
    height: 60px;
  }
  .happy-face{
   margin-right: 15px
  }

   .greet-quote{
     font-size: 20px;
   }

   .tech-quote{
     font-size: 16px;
   }
}

@-webkit-keyframes cursor-blink{
  0%{
    opacity: 1;
  }
  80%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes cursor-blink{
  0%{
    opacity: 1;
  }
  80%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@-webkit-keyframes move-down{
  0%{
    margin-top: 150px;
  }
  100%{
    margin-top: 220px;
  }
}

@keyframes move-down{
  0%{
    margin-top: 150px;
  }
  100%{
    margin-top: 220px;
  }
}
@-webkit-keyframes expand-down{
  0%{
    height:120px;
  }
  100%{
    height: 180px;
  }
}
@keyframes expand-down{
  0%{
    height:120px;
  }
  100%{
    height: 180px;
  }
}

@-webkit-keyframes text-zoom-in{
  0%{
    font-size: 5px;
  }
  100%{
    font-size: 18px;
  }
}

@keyframes text-zoom-in{
  0%{
    font-size: 5px;
  }
  100%{
    font-size: 18px;
  }
}

@-webkit-keyframes drop-down{
  0%{
    top: 40px;
  }
  100%{
    margin-top: 80px;
  }
}

@keyframes drop-down{
  0%{
    top: 40px;
  }
  100%{
    margin-top: 80px;
  }
}
@-webkit-keyframes slide-right{
  0%{
    margin-right: 1000px;
  }
  100%{
    margin-right: 7%;
  }
}
@keyframes slide-right{
  0%{
    margin-right: 1000px;
  }
  100%{
    margin-right: 7%;
  }
}
@-webkit-keyframes slide-left{
  0%{
    margin-left: 100%;
  }
  100%{
    margin-left: 0%;
  }
}
@keyframes slide-left{
  0%{
    margin-left: 100%;
  }
  100%{
    margin-left: 0%;
  }
}

@-webkit-keyframes slide-up{
  0%{
    -webkit-transform:translateY(1000px);
            transform:translateY(1000px);
  }
  100%{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}

@keyframes slide-up{
  0%{
    -webkit-transform:translateY(1000px);
            transform:translateY(1000px);
  }
  100%{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}
@-webkit-keyframes slide-down{
  0%{
    -webkit-transform:translateY(-1000px);
            transform:translateY(-1000px);
  }
  100%{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}
@keyframes slide-down{
  0%{
    -webkit-transform:translateY(-1000px);
            transform:translateY(-1000px);
  }
  100%{
    -webkit-transform:translateY(0);
            transform:translateY(0);
  }
}

@-webkit-keyframes logo-rotate{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  8%{
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  30%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-rotate{
  0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  8%{
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  30%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes smile{
  0%{
    width: 0%;
    top: 75%;
  }
  10%{
    width: 65%;
    top: 65%;
  }
  100%{
    width: 65%;
    top: 65%;
  }
}

@keyframes smile{
  0%{
    width: 0%;
    top: 75%;
  }
  10%{
    width: 65%;
    top: 65%;
  }
  100%{
    width: 65%;
    top: 65%;
  }
}
.teches{
    width: 100%;
    background-color: #F8333C;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.teches-wrap{
    width: 80%;
    height: 70%;
}

.tech-wrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50%;
}

.tech-img{
    height:100px;
}

.tech-wrap {
    font-size: 26px;
    margin-top: 10px;
    color: #fff;
}

@media (max-width:576px ){
    .tech-img{
        height:80px;
    }
    .tech-wrap p{
        font-size: 16px;
    }
}
.portfolio-wrap{
    width: 100%;
    display: flex;
        flex-wrap: wrap;
        flex-direction: row;
}

.project-wrap{
    border-radius: 10px;
    margin: 10px 10px;
    padding: 0 !important;
}
.project-image-wrap{
    position: relative;
}
.project-wrap img{
    border-radius: 20px;
    box-sizing: border-box;
    width: 400px;
}

.project-wrap:hover .project-details{
    opacity:1;
    -webkit-transition: opacity 600ms ease;
    transition: opacity 600ms ease;
}

.description{
    text-align: center;
    height: 65%;
    margin: auto;
}

.project-details{
    opacity: 0;
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    border-radius: 20px;
    padding: 30px 20px;
    color: #fff;
    background-color: #f8333de6;
}

.text-on-image{
    font-size: 18px;
    font-weight: 600;
}

@media (max-width:767px ){
    .project-image-wrap .hide{
       display: none;
    }
    .project-image-wrap .show{
        display: block;   
    }
}

@media (max-width:576px ){
    .project-wrap img{
        width: 300px;
    }
    .project-details{
        padding: 10px 10px;
    }
    .text-on-image{
        font-size: 16px;
        font-weight: 600;
    }
}
.footer{
    width: 100%;
    height: 10vh;
    max-width: auto;
}

.happy-face_footer{
    width:50px;
    height:50px;
    position: relative;
    -webkit-animation: nudge 15000ms 1600ms infinite backwards ease-in;
            animation: nudge 15000ms 1600ms infinite backwards ease-in
}

.smile_footer{
    position: absolute;
    top:65%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 65%;
    -webkit-animation: smile 7000ms 500ms infinite backwards ease-in;
            animation: smile 7000ms 500ms infinite backwards ease-in
}



@media (max-width:576px ){
  .happy-face_footer{
    -webkit-animation: nudge-small 8000ms 1600ms infinite backwards ease-in;
            animation: nudge-small 8000ms 1600ms infinite backwards ease-in
  }

  .smile_footer{
    -webkit-animation: smile 4000ms 500ms infinite backwards ease-in;
            animation: smile 4000ms 500ms infinite backwards ease-in
}
}

@-webkit-keyframes smile{
    0%{
      width: 0%;
      top: 75%;
    }
    8%{
      width: 65%;
      top: 65%;
    }
    100%{
      width: 65%;
      top: 65%;
    }
  }

@keyframes smile{
    0%{
      width: 0%;
      top: 75%;
    }
    8%{
      width: 65%;
      top: 65%;
    }
    100%{
      width: 65%;
      top: 65%;
    }
  }

