              transform: rotate(0deg);
    }
    
    25% {
      margin-left: 80%;
      -webkit-transform: rotate(1800deg);
              transform: rotate(1800deg);
    }
    50% {
      margin-left: 80%;
      -webkit-transform: rotate(1800deg);
              transform: rotate(1800deg);
    }
    
    75% {
      margin-left: 10%;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      margin-left: 10%;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
  }

  @keyframes nudge {
    0%{
